import cn from 'classnames'

const MenuItem = ({
  links,
  href,
  title,
  isCurrent = false,
  isFull = false,
  columns = 1,
  currentHref = '',
}) => {
  if (!links) {
    return (
      <li className="ecl-menu__item" data-ecl-menu-item="">
        <a href={href} className="ecl-menu__link" data-ecl-menu-link>
          {title}
        </a>
      </li>
    )
  }

  return (
    <li
      className={cn('ecl-menu__item ecl-menu__item--has-children', {
        'ecl-menu__item--current': isCurrent,
        'ecl-menu__item--full': isFull,
        [`ecl-menu__item--col${columns}`]: columns > 1,
      })}
      data-ecl-menu-item
      data-ecl-has-children
    >
      <a
        href={href}
        className={cn('ecl-menu__link', {
          'ecl-menu__link--current': isCurrent,
        })}
        data-ecl-menu-link
      >
        {title}
        <svg
          className="ecl-icon ecl-icon--xs ecl-icon--rotate-90 ecl-menu__link-icon"
          focusable="false"
          aria-hidden="true"
          viewBox="0 0 448 512"
        >
          <path d="M416 352c-8.188 0-16.38-3.125-22.62-9.375L224 173.3l-169.4 169.4c-12.5 12.5-32.75 12.5-45.25 0s-12.5-32.75 0-45.25l192-192c12.5-12.5 32.75-12.5 45.25 0l192 192c12.5 12.5 12.5 32.75 0 45.25C432.4 348.9 424.2 352 416 352z" />
        </svg>
      </a>
      <div className="ecl-menu__mega" data-ecl-menu-mega>
        <ul className="ecl-menu__sublist">
          {links.map((link) => {
            return (
              <li
                className={cn('ecl-menu__subitem', {
                  'ecl-menu__subitem--current':
                    isCurrent && currentHref === link.href,
                })}
                data-ecl-menu-subitem=""
                key={link.href}
              >
                <a
                  href={link.href}
                  className={cn('ecl-menu__sublink', {
                    'ecl-menu__sublink--current':
                      isCurrent && currentHref === link.href,
                  })}
                >
                  {link.title}
                </a>
              </li>
            )
          })}
        </ul>
      </div>
    </li>
  )
}

