import { useState } from 'react'
import { Breadcrumbs } from '../breadcrumbs'
import { Menu } from '../menu'
import styles from './header.module.css'

const Header = () => {
  const [search, setSearch] = useState('')
  const [mobileSearch, setMobileSearch] = useState(false)

  const postSearch = (e) => {
    e.preventDefault()
    window.open(`https://ec.europa.eu/search/?QueryText=${search}`)
  }

  return (
    <header>
      <div className={`container ${styles.container}`}>
        <a
          href="/clima/index_en"
          className={styles.logo__link}
          aria-label="Home (European Commission)"
        >
          <img
            src={'static/images/european-commission-logo.svg'}
            alt="European Commission Logo"
            className={styles.logo}
          />
        </a>
        <div className={styles.actions}>
          <button
            className="ecl-button ecl-button--ghost ecl-site-header-core__language-selector"
            data-ecl-language-selector=""
            aria-label="English"
            aria-controls="language-list-overlay"
          >
            <span className="ecl-site-header-core__language-icon">
              <svg
                className="ecl-icon ecl-icon--s ecl-site-header-core__icon"
                focusable="false"
                aria-hidden="true"
                viewBox="0 0 512 512"
              >
                <path d="M511.1 63.1v287.1c0 35.25-28.75 63.1-64 63.1h-144l-124.9 93.68c-7.875 5.75-19.12 .0497-19.12-9.7v-83.98h-96c-35.25 0-64-28.75-64-63.1V63.1c0-35.25 28.75-63.1 64-63.1h384C483.2 0 511.1 28.75 511.1 63.1z" />
              </svg>
              <span className="ecl-site-header-core__language-code">en</span>
            </span>
            English
          </button>
          <div
            className="ecl-site-header-standardised__search-container"
            style={{ flexGrow: 0 }}
          >
            <a
              className="ecl-button ecl-button--ghost ecl-site-header-core__search-toggle"
              href="/clima/index_en"
              data-ecl-search-toggle="true"
              aria-controls="search-form-id"
              aria-expanded={mobileSearch}
              onClick={(e) =>
                e.preventDefault() || setMobileSearch(!mobileSearch)
              }
            >
              <img
                className="ecl-icon ecl-icon--xs"
                focusable="false"
                aria-hidden="false"
                src={'static/images/search.svg'}
                alt="search"
                style={{ width: '20px', height: '20px' }}
              />
              Search
            </a>
            <form
              className={`ecl-search-form ecl-site-header-core__search ${
                mobileSearch && `ecl-site-header-core__search--active`
              }`}
              role="search"
              data-ecl-search-form=""
              id="search-form-id"
            >
              <div className="ecl-form-group">
                <label
                  htmlFor="search-input-id"
                  className="ecl-form-label ecl-search-form__label"
                >
                  Search
                </label>
                <input
                  type="search"
                  id="search-input-id"
                  className="ecl-text-input ecl-text-input--m ecl-search-form__text-input"
                  placeholder=""
                  onChange={(e) => setSearch(e.target.value)}
                />
              </div>
              <button
                className="ecl-button ecl-button--search ecl-search-form__button"
                type="submit"
                aria-label="Search"
                onClick={postSearch}
              >
                <span className="ecl-button__container">
                  <span className="ecl-button__label" data-ecl-label="true">
                    Search
                  </span>
                  <img
                    className="ecl-icon ecl-icon--xs ecl-button__icon ecl-button__icon--after"
                    focusable="false"
                    aria-hidden="false"
                    src={'static/images/search.svg'}
                    alt="search"
                  />
                </span>
              </button>
            </form>
          </div>
        </div>
      </div>
      <Menu />
      <Breadcrumbs />
    </header>
  )
}

export default Header
